@tailwind base;
@tailwind components;
@tailwind utilities;


body * {
    font-family: 'Shabnam FD', serif;
    word-break: break-word;
}

.box {
    width: 80%;
    position: absolute;
    height: 2px;
    border-radius: 10px;
    top: 20%;
    left: 50%;
    transform: translateX(-50%);
    animation: moveBox 2s ease-in-out infinite alternate;
}

@keyframes moveBox {
    from {
        top: 20%;
    }
    to {
        top: 80%;
    }
}


.qr-video-container   {
    width: 90%;
    height: 90%;
    top:5%;
    right:5%;
    position: relative;
    object-fit: cover; /* Ensures the video covers the square */
}
.qr-video-container video {
    width: 100%;
    height: 100%;
    object-fit: cover; /* Ensures the video covers the square */
}


.error-title {
    font-size: 210px;
    font-weight: 900;
    text-shadow: 4px 4px 0 #fff, 6px 6px 0 #263238;
    line-height: 210px;
}

@media (max-width: 1024px) {
    .error-title {
        font-size: 80px;
        line-height: 80px;
    }
}